
*{
    transition: all .2s;
}
body{
    font-family: 'Roboto';
}
a{
    text-decoration: none;
}
.popup_container{
    width: 100%;
    height: 100vh;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.7);
    display: none;
    justify-content: center;
    align-items: center;
    z-index: 2;
}
.popup_container:target{
    display: flex;
}
.popup_wrapper{
    width: 791px;
    background-color: #fff;
    border-radius: 19px;
}
.popup_menu{
    padding: 19px 35px 19px 35px;
    background-color: #F6F1ED;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 19px 19px 0 0;
    border: solid #e4e4e4;
    border-top-width: 0;
    border-right-width: 0;
    border-bottom-width: 1px;
    border-left-width: 0;
}
.popup_title{
    color: #3C3C3C;
    font-size: 24px;
    font-weight: 900;
    margin: 0;
}
.popup_close{
    width: 35px;
    height: 35px;
    background: #F6F1ED url(data:image/svg+xml;base64,) no-repeat;
    background-position: center center;
    background-size: 70%;
    display: block;
}
.popup_close:hover{
    background-color: #e8d6c4;
    border-radius: 100%;
}
.body{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.content{
    width: 775px;
    border-radius: 19px;
    box-shadow: 0px 0px 19px rgba(38, 35, 35, 0.25);
}
.menu{
    padding: 0 35px 0 35px;
    background-color: #F6F1ED;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 19px 19px 0 0;
    border: solid #e4e4e4;
    border-top-width: 0;
    border-right-width: 0;
    border-bottom-width: 1px;
    border-left-width: 0;
}
.title{
    color: #3C3C3C;
    font-size: 24px;
    margin: 0;
    font-weight: 900;
}
.blocks{
    width: 100%;
    max-height: 524px;
    overflow: auto;
}
.inner{
    position: relative; 
    height: auto;
    z-index: 1;
}
.block, .block_active{
    width: 108px;
    height: 86px;
    padding: 21px;
    background-color: #F6F1ED;
    border-radius: 14px;
    margin: 0 0 35px 35px;
    float: left;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.block_delete{ 
    width: 108px;
    height: 86px;
    position: absolute;
    display: none;
    background: #F6F1ED url(data:image/svg+xml;base64,) no-repeat;
    background-position: center center;
    background-size: 60%;
    cursor: pointer;
    outline: none;
    border: 0;
}
.block:hover .block_delete{
    display: block;
}
.block_title{
    color: #3C3C3C;
    font-size: 18px;
    margin: 0;
    font-weight: 500;
}
.block_course{
    color: #3C3C3C;
    font-size: 22px;
    margin: 0;
    font-weight: 900;
}
/*.block_active{
    width: 108px;
    height: 86px;
    padding: 21px;
    background-color: #F6F1ED;
    border-radius: 14px;
    margin: 0 0 35px 35px;
    float: left;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}*/
.block_active:hover{
    box-shadow: 0px 0px 6px rgba(255, 1, 1, 0.25);
}
.block_add{
    width: 108px;
    height: 86px;
    background: url(data:image/svg+xml;base64,) no-repeat;
    background-position: center center;
    background-size: 70%;
}
.popup_currency_container{
    height: 335px;
    overflow: auto;
}
.popup_currency{
    width: 175px;
    padding: 15px 21px 15px 21px;
    background-color: #F6F1ED;
    margin: 0 0 35px 35px;
    border-radius: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    float: left;
}
.popup_currency:hover{
    box-shadow: 0px 0px 6px rgba(255, 1, 1, 0.25);
}
.popup_currency_title_container{
    width: 140px;
}
.popup_currency_title{
    color: #3C3C3C;
    font-size: 18px;
    margin: 0;
    font-weight: 500;
}
.popup_currency_add{
    width: 35px;
    height: 35px;
    background: #7efb94 url(data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgMCAyNCAyNCIgZmlsbD0iIzNDM0MzQyIgd2lkdGg9IjQ4cHgiIGhlaWdodD0iNDhweCI+PHBhdGggZD0iTTAgMGgyNHYyNEgweiIgZmlsbD0ibm9uZSIvPjxwYXRoIGQ9Ik0xOSAxM2gtNnY2aC0ydi02SDV2LTJoNlY1aDJ2Nmg2djJ6Ii8+PC9zdmc+) no-repeat;
    background-position: center center;
    background-size: 65%;
    border: 0;
    cursor: pointer;
    outline: none;
    border-radius: 100%;
}
.popup_currency_add:hover{
    background-color: #71f188;
}
.update_container{
    display: flex;
    align-items: center;
}
.update_good, .update_error{
    color: #2BCE28;
    font-size: 14px;
    font-weight: 500;
    margin: 0 22px 0 0;
}
.update_error{
    color: #ff0000;
}
.update{
    width: 35px;
    height: 35px;
    background: #F6F1ED url(data:image/svg+xml;base64,) no-repeat;
    background-position: center center;
    background-size: 70%;
    border-radius: 7px;
    box-shadow: 0px 0px 6px #56565640;
    border: 0;
    margin: 19px 0 19px 0;
    cursor: pointer;
    outline: none;
}
.update:hover{
    box-shadow: 0px 0px 6px rgba(255, 1, 1, 0.25);
}
